import {Component, ElementRef, OnDestroy, OnInit, ViewChild} from '@angular/core';
import {Store} from '@ngrx/store';
import {NgbDate, NgbCalendar, NgbDateParserFormatter} from '@ng-bootstrap/ng-bootstrap';
import {FormArray, FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {DemandState} from '../../store/demand.reducer';
import * as endPoints from '../../../shared/serverEndpoints';
import * as DemandActions from '../../store/demand.actions';
import {Subscription} from 'rxjs';
import {ActivatedRoute, Router} from '@angular/router';
import {HttpClient, HttpResponse} from '@angular/common/http';
 
@Component({
  selector: 'app-trekslist',
  templateUrl: './trekslist.component.html',
  styleUrls: ['./trekslist.component.css']
})
export class TrekslistComponent implements OnInit, OnDestroy {
  @ViewChild('location', {static: false}) location: ElementRef;
  allTreks: Array<any>;
  statewiseTreks:Array<any>;
  trekSubscription: Subscription;
  requestData: {
    location: null,
    maxPeople: null,
    date: null
  };
  isSearched: boolean;
  show_all:boolean;
  no_treks:boolean;
  state:null;
  reletive_treks:boolean;
  trek_date:NgbDate;
  basicForm: FormGroup;

  constructor(private store: Store<DemandState>,private fb: FormBuilder, private route: ActivatedRoute, private router: Router, private http: HttpClient,private calendar: NgbCalendar,
    public formatter: NgbDateParserFormatter,) {
  }
  ngOnInit() {
    this.http.post(endPoints.trekList, this.requestData,{observe: 'response'}).subscribe(
      (response: HttpResponse<any>) => {
        if (response.status === 200) {
          this.store.dispatch(new DemandActions.SetAllTrek(response.body));
        }
      }
    );
    this.isSearched = false;
    this.reletive_treks = false;
    this.no_treks = false;
    this.show_all =false;

    this.trekSubscription = this.store.select('demand').subscribe(
      demandState => {
        if (demandState.trekDemand.allTreks) {
          this.allTreks = [];
          demandState.trekDemand.allTreks.forEach(
            trek => {
              this.allTreks.push(trek);
            }
          );
        }
      }
    );

    this.basicForm = this.fb.group({
      location: [''],
      startDate:[''],
      difficultyLevel: [''],
    });
  }


  search(form){
    this.basicForm.controls[`location`].patchValue(this.location.nativeElement.value);
    console.log(this.basicForm.value);
    this.http.post(endPoints.trekQuery, this.basicForm.value, {observe: 'response'}).subscribe(
        (response: HttpResponse<any>) => {
          if (response.status === 200) {
          this.isSearched = false;
          this.reletive_treks = false;
          this.no_treks = false;
          this.show_all =true;
            this.store.dispatch(new DemandActions.SetAllTrek(response.body));
          }
          if(response.status == 201)
          {
            this.store.dispatch(new DemandActions.SetAllTrek(response.body));
            this.isSearched = false;
            this.no_treks = false;
            this.reletive_treks = true;
          }
          if(response.status == 202)
          {
            this.isSearched = false;
            this.reletive_treks = false;
            this.show_all = false;
            this.no_treks = true;
          }
        }                                 
    );
    this.trekSubscription = this.store.select('demand').subscribe(
      demandState => {
        if (demandState.trekDemand.allTreks) {
          this.allTreks = [];
          demandState.trekDemand.allTreks.forEach(
            trek => {
              this.allTreks.push(trek);
            }
          );
        }
      }
    );
  //this.dispatch(this.basicForm.value);
  //console.log(this.basicForm.value);
    this.show_all = true;
  }

  searchList() {
    this.show_all = true;
    document.body.scrollTop = 0;
    document.documentElement.scrollTop = 0;
    console.log(this.allTreks);
    console.log(this.show_all );
  }

  statewiselist(id,state_name){
    this.state = state_name;
    this.statewiseTreks = this.allTreks.filter(obj => {
    return obj._id === id
    })
    this.reletive_treks = false;
    this.no_treks = false;
    this.isSearched = true;
    document.body.scrollTop = 0;
    document.documentElement.scrollTop = 0;
  }

  navigateToTrekDetail(id: string) {
    document.body.scrollTop = 0;
    document.documentElement.scrollTop = 0;
    this.router.navigate(['../details', id], {relativeTo: this.route});
  }
  lazyLoading(){
    this.http.post(endPoints.trekList, this.requestData,{observe: 'response'}).subscribe(
      (response: HttpResponse<any>) => {
        if (response.status === 200) {
          this.store.dispatch(new DemandActions.SetAllTrek(response.body));
        }
      }
    );
    this.isSearched = false;
    this.reletive_treks = false;
    this.no_treks = false;
    this.show_all =false;

    this.trekSubscription = this.store.select('demand').subscribe(
      demandState => {
        if (demandState.trekDemand.allTreks) {
          this.allTreks = [];
          demandState.trekDemand.allTreks.forEach(
            trek => {
              this.allTreks.push(trek);
            }
          );
        }
      }
    );
  }

  ngOnDestroy(): void {
    this.allTreks = null;
    this.trekSubscription.unsubscribe();
  }

}
