import {Component, OnInit} from '@angular/core';
import {Store} from '@ngrx/store';
import {DemandState} from '../../store/demand.reducer';
import * as DemandActions from '../../store/demand.actions';
import {TreksModel} from '../../../roles/business/treks/treks.model';
import {ActivatedRoute, ParamMap, Router} from '@angular/router';
import {FormBuilder, FormGroup} from '@angular/forms';
import {HttpClient, HttpResponse} from '@angular/common/http';
import * as endPoints from '../../../shared/serverEndpoints';

@Component({
  selector: 'app-treksdetails',
  templateUrl: './treksdetails.component.html',
  styleUrls: ['./treksdetails.component.css']
})
export class TreksdetailsComponent implements OnInit {
  queryForm: FormGroup;
  children = 0;
  adult = 1;
  id: string;
  activeTrek: TreksModel;
  similarTrek: TreksModel[] = [];
  trekBannerImages = new Array(5);
  finalPrice = 0;

  selectedDatesIndex = 0;

  constructor(
    private store: Store<DemandState>,
    private router: Router,
    private route: ActivatedRoute,
    private fb: FormBuilder,
    private http: HttpClient
  ) {
  }

  ngOnInit() {

    this.route.paramMap.subscribe((params: ParamMap) => {
        if (params.get('id')) {
          this.id = params.get('id');

          this.store.dispatch(new DemandActions.TryFetchTrek({_id: this.id}));

          // initializing form to get pricing info
          this.queryForm = this.fb.group({
            days: this.fb.control(''),
            adults: this.fb.control(this.adult),
            children: this.fb.control(this.children),
            _id: this.fb.control(this.id),
          });

          this.getPricing();
        }
      }
    );
    this.store.select('demand').subscribe(
      demandState => {
        if (demandState.trekDemand.activeTrek) {
          this.activeTrek = demandState.trekDemand.activeTrek;
          this.trekBannerImages = demandState.trekDemand.activeTrek.trekImage;
          if (demandState.trekDemand.activeTrek.similarTreks.length > 0) {
            demandState.trekDemand.activeTrek.similarTreks.forEach(trek => {
              this.similarTrek.push(trek);
            });
          }
          console.log(this.similarTrek);
        }

      }
    );
  }

  adultControl(x) {
    if (!(this.adult < 2 && x < 0)) {
      this.adult += x;
    }
    this.queryForm.controls[`adults`].patchValue(this.adult);
    this.getPricing();
  }

  childControl(x) {
    if (!(this.children < 1 && x < 0)) {
      this.children += x;
    }
    this.queryForm.controls[`children`].patchValue(this.children);
    this.getPricing();
  }

  getPricing() { // http request to fetch final pricing
    this.http.post(endPoints.trekPricing, this.queryForm.value, {observe: 'response'}).subscribe(
      (response: HttpResponse<any>) => {
        if (response.status === 200) {
          this.finalPrice = response.body;
        }
      }
    );
  }

  selectedDatesIndexFunction(val: number) {
    this.selectedDatesIndex = val;
  }

  onSubmitQueryForm() {
    this.queryForm.controls[`days`].patchValue(this.activeTrek.trekDates[this.selectedDatesIndex]);
    this.store.dispatch(new DemandActions.SetActiveTrekDetails(this.queryForm.value));
    this.store.dispatch(new DemandActions.SetPrice(this.finalPrice));
    this.router.navigate(['../../checkout'], {relativeTo: this.route});
  }

}
