import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {TreksComponent} from './treks.component';
import {TrekslistComponent} from './trekslist/trekslist.component';
import {TreksdetailsComponent} from './treksdetails/treksdetails.component';
import {TrekscheckoutComponent} from './trekscheckout/trekscheckout.component';


const routes: Routes = [
  {
    path: '', component: TreksComponent, children: [
      {path: 'list', component: TrekslistComponent},
      {path: 'details/:id', component: TreksdetailsComponent},
      {path: 'checkout', component: TrekscheckoutComponent}
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TreksRoutingModule {
}
