import {Component, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {TreksModel} from '../treks.model';
import {Store} from '@ngrx/store';
import {TrekState} from '../store/treks.reducer';
import * as TrekAction from '../store/treks.actions';

@Component({
  selector: 'app-managetrek',
  templateUrl: './managetrek.component.html',
  styleUrls: ['./managetrek.component.css']
})
export class ManagetrekComponent implements OnInit {

  treks: TreksModel[];
  currentTrek: TreksModel;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private store: Store<TrekState>
  ) {
  }

  ngOnInit() {
    this.store.dispatch(new TrekAction.TryFetchTreks());
    this.store.select('treks').subscribe(trekState => {
      this.treks = trekState.treks;
    });
  }

  editTrek(index) {
    this.router.navigate(['../editTrek', index], {relativeTo: this.route});
  }

  deleteTrek(index: number) {
    this.currentTrek = this.treks[index];
    this.currentTrek.delFlg = true;
    this.store.dispatch(new TrekAction.TrySaveTrek(this.currentTrek));
    this.store.dispatch(new TrekAction.TryFetchTreks());
  }
}
