import {Action} from '@ngrx/store';
import {TreksModel} from '../treks.model';

export const TRY_SAVE_TREK = 'TRY_SAVE_TREK';
export const TRY_FETCH_TREKS = 'TRY_FETCH_TREKS';
export const TRY_UPLOAD_TREK_IMAGE = 'TRY_UPLOAD_TREK_IMAGE';
export const SET_TREKS = 'SET_TREKS';
export const SET_ACTIVE_TREK = 'SET_ACTIVE_TREK';

export class TryUploadTrekImage implements Action {
  readonly type = TRY_UPLOAD_TREK_IMAGE;

  constructor(public payload: any) {
  }
}

export class TryFetchTreks implements Action {
  readonly type = TRY_FETCH_TREKS;
}

export class TrySaveTrek implements Action {
  readonly type = TRY_SAVE_TREK;

  constructor(public payload: TreksModel) {
  }
}


export class SetTreks implements Action {
  readonly type = SET_TREKS;

  constructor(public payload: TreksModel[]) {
  }
}

export class SetActiveTrek implements Action {
  readonly type = SET_ACTIVE_TREK;

  constructor(public payload: TreksModel) {
  }
}

export type TreksActions =
  SetActiveTrek |
  TryUploadTrekImage |
  SetTreks |
  TryFetchTreks |
  TrySaveTrek ;

