import {Component, ElementRef, OnDestroy, OnInit, ViewChild} from '@angular/core';
import {FormArray, FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {NgbDate, NgbCalendar, NgbDateParserFormatter} from '@ng-bootstrap/ng-bootstrap';
import {TreksModel} from '../treks.model';
import {Store} from '@ngrx/store';
import {TrekState} from '../store/treks.reducer';
import * as TrekActions from '../store/treks.actions';
import {ActivatedRoute, ParamMap, Router} from '@angular/router';
import {ToastService} from '../../../../shared/toast/toast.service';
import * as EventActions from '../../events/store/events.actions';

interface TrekDays {
  activityDescription;
  trekDayImage;
  locations;
  provided: {
    water: [false],
    food: [false],
    sleepingBag: [false]
  };
  carry;
  transport;
  title;
  meetup;
  notes;
}

@Component({
  selector: 'app-addtrek',
  templateUrl: './addtrek.component.html',
  styleUrls: ['./addtrek.component.css']
})
export class AddtrekComponent implements OnInit, OnDestroy {
  @ViewChild('daylocation', {static: false}) daylocation: ElementRef;
  @ViewChild('location', {static: false}) location: ElementRef;
  @ViewChild('t', {static: false}) tabSet;

  basicForm: FormGroup;
  trekForm: FormGroup;
  settingForm: FormGroup;
  trekDayDetails: FormArray;

  finalTrek = new TreksModel();

  hoveredDate: NgbDate;
  fromDate: NgbDate;
  toDate: NgbDate;
  days: any;
  currentDay: number;
  trekDaysArray: Array<any>;
  trekDaysArrayDummy: Array<any>;

  trekDayImg: File = null;
  trekImg: File = null;
  trekImgArray: Array<any> = [];
  imgPreviewUrlTrekDay: any = '../../../assets/icons/uploadPic.png';
  imgPreviewUrlTrek: any = '../../../assets/icons/uploadPic.png';

  id: number;
  editMode = false;

  constructor(
    private router: Router,
    private fb: FormBuilder,
    private calendar: NgbCalendar,
    public formatter: NgbDateParserFormatter,
    private store: Store<TrekState>,
    private route: ActivatedRoute,
    private toastService: ToastService
  ) {
    this.currentDay = 0;
    this.days = 3;
    this.trekDaysArrayDummy = Array(this.days).fill(0);
    this.trekDaysArray = [];
    this.fromDate = calendar.getToday();
    this.toDate = calendar.getNext(calendar.getToday(), 'd', this.days - 1);
  }

  ngOnInit() {

    this.basicForm = this.fb.group({
      company: ['Wander Tribe'],
      tourGuide:[''],
      guideDescription:[''],
      // trekTitle: ['', Validators.required],
      // location: ['', Validators.required],
      // temperature:['',Validators.required],
      // altitude:['',Validators.required],
      // trekDates: this.fb.array([], Validators.required),
      // totalDays: ['', Validators.required],
      trekTitle: [''],
      location: ['',],
      temperature:[''],
      altitude:[''],
      trekDates: this.fb.array([], Validators.required),
      totalDays: [''],
      languages: this.fb.group({
        hindi: [false],
        english: [false]
      }),
      difficultyLevel: ['easy', Validators.required],
      ownerDescription: [''],
      trekImage: this.fb.array([]),
      equipmentDescription:['']
      //naturalDisasterResponsePlan: [''],
      //emergencyResponsePlan: [''],
      //medicalResponsePlan: [''],
      //cancellationPolicy: [''],
    });

    this.trekForm = this.fb.group({
      activityDescription: [''],
      trekDayImage: [''],
      locations: ['', Validators.required],
      provided: this.fb.group({
        water: [false],
        food: [false],
        sleepingBag: [false]
      }),
      carry: [''],
      transport: [''],
      title: ['', Validators.required],
      meetup: [''],
      notes: [''],
    });

    this.settingForm = this.fb.group({
      //trekImage: this.fb.array([]),
      naturalDisasterResponsePlan: [''],
      emergencyResponsePlan: [''],
      medicalResponsePlan: [''],
      cancellationPolicy: [''],
      provided: this.fb.group({
        accommodation: [false],
        food: [false],
        transport: [false]
      }),
      carry: [''],
      pricePerPerson: [''],
      minPeople: [''],
      maxPeople: [''],
      todoPlan: [''],
      //trekTitle: ['', Validators.required],
    });


    this.trekDayDetails = this.fb.array([]);

    this.store.select('treks').subscribe(trekState => {
      if (!this.editMode) {

        if (trekState.activeTrek) {
          this.finalTrek = trekState.activeTrek;
          if (trekState.activeTrek.trekDayDetails) {
            this.trekDaysArray = trekState.activeTrek.trekDayDetails;
          }
        }
      }
    });
    // for editing a trek
    this.route.paramMap.subscribe((params: ParamMap) => {
        if (params.get('id')) {
          this.editMode = true;

          this.id = +params.get('id');
          this.store.select('treks').subscribe(trekState => {
            this.finalTrek = trekState.treks[this.id];


            if (this.finalTrek) {
              this.basicForm.patchValue(this.finalTrek);
              this.settingForm.patchValue(this.finalTrek);

              this.finalTrek.trekDayDetails.forEach((value: TrekDays) => {
                this.trekDayDetails.push(this.addTrekDayDetailGroup(value));
              });

              this.trekForm.setValue(this.trekDayDetails.value[0]);
            } else {
              this.router.navigate(['../../manageTrek'], {relativeTo: this.route});
            }
          });
        }
      }
    );
  }

  addTrekDayDetailGroup(value: TrekDays) {
    const group = this.fb.group({
      activityDescription: [value.activityDescription],
      trekDayImage: [''],
      locations: [value.locations],
      provided: this.fb.group({
        water: [value.provided.water],
        food: [value.provided.food],
        sleepingBag: [value.provided.sleepingBag]
      }),
      carry: [value.carry],
      transport: [value.transport],
      title: [value.title],
      meetup: [value.meetup],
      notes: [value.notes],
    });

    return group;

  }

  onDateSelection(date: NgbDate) {
    if (!this.fromDate && !this.toDate) {
      this.fromDate = date;
    } else if (this.fromDate && !this.toDate && date.after(this.fromDate)) {
      this.toDate = date;
    } else {
      this.toDate = null;
      this.fromDate = date;
    }

    if (this.toDate && this.fromDate) {
      this.days = Math.floor((Date.parse(this.toDate.month + '/' + this.toDate.day + '/' + this.toDate.year) - Date.parse(this.fromDate.month + '/' + this.fromDate.day + '/' + this.fromDate.year)) / 86400000);
      this.days += 1;
    }
    this.trekDaysArrayDummy = Array(this.days).fill(0);
  }

  isHovered(date: NgbDate) {
    return this.fromDate && !this.toDate && this.hoveredDate && date.after(this.fromDate) && date.before(this.hoveredDate);
  }

  isInside(date: NgbDate) {
    return date.after(this.fromDate) && date.before(this.toDate);
  }

  isRange(date: NgbDate) {
    return date.equals(this.fromDate) || date.equals(this.toDate) || this.isInside(date) || this.isHovered(date);
  }

  validateInput(currentValue: NgbDate, input: string): NgbDate {
    const parsed = this.formatter.parse(input);
    return parsed && this.calendar.isValid(NgbDate.from(parsed)) ? NgbDate.from(parsed) : currentValue;
  }

  addTrekDate() {
    let ngbDate = this.fromDate;
    let fromdate = new Date(ngbDate.year, ngbDate.month-1, ngbDate.day);

    let ngbDate1 = this.toDate;
    let todate = new Date(ngbDate1.year, ngbDate1.month-1, ngbDate1.day);

    const x = {
      startDate: fromdate,
      toDate:todate,
    };
    const obj = new FormControl(x);
    (this.basicForm.controls[`trekDates`] as FormArray).push(obj);
    this.basicForm.patchValue({totalDays: this.days});
  }

  removeTrekDate(i: number) {
    (this.basicForm.controls[`trekDates`] as FormArray).removeAt(i);
  }

  imgPreviewTrekDay(fileInput: any) {
    this.trekDayImg = fileInput.target.files[0] as File;
    const mimeType = this.trekDayImg.type;
    if (mimeType.match(/image\/*/) == null) {
      return;
    }
    const reader = new FileReader();
    reader.readAsDataURL(this.trekDayImg);
    reader.onload = () => {
      this.imgPreviewUrlTrekDay = reader.result;
    };
  }

  imgPreviewTrek(fileInput: any) {
    const files = fileInput.target.files;
    if (files.length >= 1) {
      for (const image of files) {
        this.trekImgArray.push(image);
        const reader = new FileReader();
        reader.onload = (e: any) => {
          (this.basicForm.get('trekImage') as FormArray).push(
            this.fb.group({
              url: e.target.result
            })
          );
        };
        reader.readAsDataURL(image);
      }
    } else {
      this.toastService.showDanger('Please select more than 4 images!');
    }
  }

  setTrekDay(it: number) {
    this.currentDay = it;
    if (this.trekDayDetails.value[it]) {
      this.trekForm.setValue(this.trekDayDetails.value[it]);
    } else {
      this.trekForm.reset();
    }
  }

  basicFormSubmit() {
    this.basicForm.controls[`location`].patchValue(this.location.nativeElement.value);
    this.dispatch(this.basicForm.value);
    this.tabSet.select('trekForm');
  }

  trekFormSubmit() {
    this.trekForm.controls[`locations`].patchValue(this.daylocation.nativeElement.value);
    if (this.trekDayDetails && this.trekDayDetails.value[this.currentDay]) {
      this.trekDayDetails.insert(this.currentDay, this.addTrekDayDetailGroup(this.trekForm.value));
    } else {
      this.trekDayDetails.push(this.addTrekDayDetailGroup(this.trekForm.value));
    }
    this.dispatch({trekDayDetails: this.trekDayDetails.value});

    const data = new FormData();
    data.append('_id', this.finalTrek._id);
    data.append('trekDayImage', this.trekDayImg);
    data.append('index', (this.currentDay).toString());
    this.store.dispatch(new TrekActions.TryUploadTrekImage(
      data
    ));

    if (this.currentDay === this.days - 1) {
      this.tabSet.select('settingForm');
      this.currentDay = 0;
    } else {
      this.currentDay += 1;
    }
  }

  settingFormSubmit() {
    this.dispatch(this.settingForm.value);
    if (this.trekImgArray.length) {
      const data = new FormData();
      data.append('_id', this.finalTrek._id);
      Array.from(this.trekImgArray).forEach(image => {
        data.append('trekImage[]', image);
      });
      this.store.dispatch(new TrekActions.TryUploadTrekImage(
        data
      ));
    }
    this.router.navigate(['../manageTrek'], {relativeTo: this.route});
  }

  private dispatch(obj) {
    Object.assign(this.finalTrek, obj);
    this.store.dispatch(new TrekActions.TrySaveTrek(this.finalTrek));
  }

  ngOnDestroy(): void {
    this.finalTrek = null;
    this.store.dispatch(new TrekActions.SetActiveTrek(null));
  }
}
