import {AppState} from '../../../../store/app.reducer';
import {TreksModel} from '../treks.model';
import {TreksActions} from './treks.actions';

export interface TrekState extends AppState {
  treks: State;
}

export interface State {
  treks: TreksModel[];
  activeTrek: TreksModel;
}

const initialState: State = {
  activeTrek: null,
  treks: []
};

export function TreksReducer(state = initialState, action: TreksActions) {
  switch (action.type) {
    case 'SET_ACTIVE_TREK': {
      return {
        ...state,
        activeTrek: action.payload
      };
    }
    case 'SET_TREKS': {
      return {
        ...state,
        treks: action.payload
      };
    }
    default: {
      return {
        ...state
      };
    }
  }
}
