import * as endPoints from '../../../../shared/serverEndpoints';
import {Injectable} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {HttpClient, HttpResponse} from '@angular/common/http';
import * as TrekActions from './treks.actions';
import {map, switchMap} from 'rxjs/operators';
import {TreksModel} from '../treks.model';

@Injectable()
export class TreksEffects {
  constructor(public action: Actions, public http: HttpClient) {
  }

  @Effect()
  uploadImage = this.action.pipe(
    ofType(TrekActions.TRY_UPLOAD_TREK_IMAGE),
    map((data: TrekActions.TryUploadTrekImage) => {
      return data.payload;
    }),
    switchMap((data: any) => {
      return this.http.post(endPoints.uploadTrekImage, data, {observe: 'response'});
    }), switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        return [
          {
            type: TrekActions.SET_ACTIVE_TREK,
            payload: response.body
          }
        ];
      } else {
        alert('Some error occured !');
        return [];
      }
    })
  );

  @Effect()
  editTrek = this.action.pipe(
    ofType(TrekActions.TRY_SAVE_TREK),
    map((data: TrekActions.TrySaveTrek) => {
      return data.payload;
    }),
    switchMap((data: TreksModel) => {
      return this.http.post(endPoints.saveTrek, data, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        return [
          {
            type: TrekActions.SET_ACTIVE_TREK,
            payload: response.body
          }
        ];
      } else {
        alert('Some error occured !');
        return [];
      }
    })
  );


  @Effect()
  fetchTreks = this.action.pipe(
    ofType(TrekActions.TRY_FETCH_TREKS),
    switchMap(() => {
      return this.http.get(endPoints.fetchTreks, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        console.log('inside fetch trek 200');
        return [
          {
            type: TrekActions.SET_TREKS,
            payload: response.body
          }
        ];
      } else {
        // alert('Some error occured !');
        return [];
      }
    })
  );


}
