import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {TreksComponent} from './treks.component';
import {DashboardComponent} from './dashboard/dashboard.component';
import {AddtrekComponent} from './addtrek/addtrek.component';
import {ManagetrekComponent} from './managetrek/managetrek.component';


const routes: Routes = [
  {
    path: '', component: TreksComponent, children: [
      {path: '', redirectTo: 'dashboard'},
      {path: 'dashboard', component: DashboardComponent},
      {path: 'addTrek', component: AddtrekComponent},
      {path: 'editTrek/:id', component: AddtrekComponent},
      {path: 'manageTrek', component: ManagetrekComponent},
    ]
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TreksBusinessRoutingModule {
}
